body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --pisang: #ffff4e;
  --pan: #ff9b00;
  --pink-otak: #e36671;
  --bgc-primary: #f7f7f7;
  --text-primary: #4d4b45;
  --pisang-biru: #4ea7ff;
  --pisang-merah: #ff4e4f;
  --gmail: #dd4b39;
  --whatsapp: #25D366;
  --facebook: #3b5998;
  --twitter: #00aced;
  --instagram: #d6249f;
  --instagram: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);

}

.in-construction {
  max-width: 400px;
  height: 80vh;
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.in-construction .icon {
  font-size: 6em;
}

body {
  /* background-color: var(--bgc-primary);
  color: var(--text-primary); */
  background-color: #ffff4e;
  background-color: var(--pisang);
  color: #4d4b45;
  color: var(--text-primary);
}

.navbar {
  /* background-color: var(--pisang); */
  background-color: transparent;
}

.navbar .logo-title {
  /* img logo
  height: 24px;
  padding: 0;
  margin: 0;
  width: auto; */
  color: #ff9b00;
  color: var(--pan);
  font-family: "Helvetica Neue", "Helvetica", "Arial", "sans-serif";
  /* font-family: Tesla; */
}

.home {
  /* width: 100vw; */
  /* if uncomment above, side scrolling appear */
  height: 60vh;
  display: -webkit-flex;
  display: flex;
  /* justify-content: center; */
  /* align-items: center; */
  margin-top: 107px;
}

.home .logo {
  max-width: 500px;
  position: absolute;
  z-index: -5;
  opacity: 0.5;
  -webkit-transform: translate(40px, 200px) rotate(-20deg);
          transform: translate(40px, 200px) rotate(-20deg);
}

.home .title {
  /* z-index: 5; */
  text-align: left;
  color: aliceblue;
  text-shadow: 3px 3px grey;
}

.home .title .transparent {
  color: transparent;
  text-shadow: 3px 3px transparent;
}

.main-pages {
  padding-top: 20px;
  padding-bottom: 20px;
}

.main-pages .title {
  text-align: center;
  /* height: 10vh; */
  /* padding: 14px 0; */
}

/* White Box */
.white-box {
  background: #f7f7f7;
  border-radius: 15px;
  padding: 20px;
  margin: 24px 14px;
  box-shadow: 2px 2px 8px #aaa;
}

/* About Us */
/* #about-us {
  background-color: var(--pisang-biru);
  color: azure;
} */

/* Projects */
#projects {
  /* background-color: #a74eff;
  color: azure; */
  text-align: center;
}

#projects i {
  font-size: 5em;
  color: #aaa;
  margin: 24px;
}

/* Contact Us */
/* #contact-us {
  text-align: left;
} */

/* #contact-us .btn-container { */
  /* display: flex;
  justify-content: flex-start;
  align-items: center; */
/* } */

#contact-us .btn-long {
  width: 100%;
  height: 60px;
  margin: 15px 0;
  padding: auto;
  box-shadow: 3px 3px 5px #aaa;
  /* background-color: #00ff00;
  border: 5px solid #4d4b45; */
  /* color: #f7f7f7; */
  /* text-align: left; */
}

#contact-us .btn-long:hover {
  box-shadow: 15px 15px 20px #aaa;
}

#contact-us .whatsapp {
  background-color: #25D366;
  background-color: var(--whatsapp);
  border: #25D366;
  border: var(--whatsapp);
}

#contact-us .gmail {
  background-color: #dd4b39;
  background-color: var(--gmail);
  border: #dd4b39;
  border: var(--gmail);
}

#contact-us .facebook {
  background-color: #3b5998;
  background-color: var(--facebook);
  border: #3b5998;
  border: var(--facebook);
}

#contact-us .twitter {
  background-color: #00aced;
  background-color: var(--twitter);
  border: #00aced;
  border: var(--twitter);
}

#contact-us .instagram {
  background: -webkit-radial-gradient(bottom left, #fdf497, #fd5949,#d6249f,#285AEB);
  background: radial-gradient(to top right, #fdf497, #fd5949,#d6249f,#285AEB);
  border: none;
}

#contact-us i {
  font-size: 1.3em;
}

#contact-us .name {
  font-weight: 450;
  font-size: 1.1em;
}

#contact-us .location {
  font-size: 0.8em;
}

/* Services */
#services {
  /* background-color: var(--pisang-merah);
  color: var(--bgc-primary); */
  text-align: center;
}

/* footer */
.page-footer {
  background-color: #4d4b45;
  background-color: var(--text-primary);
  color: #f7f7f7;
  color: var(--bgc-primary);
}

.page-footer .icon-container {
  width: 80vw;
  margin: auto;
  padding: 30px 0;
  font-size: 2em;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.page-footer a {
  color: #f7f7f7;
  color: var(--bgc-primary);
  padding: 0;
  margin: 0;
}

.page-footer a:hover {
  color: #ffff4e;
  color: var(--pisang);
}

@media (min-width: 992px) {
  /* White Box */
  .white-box {
    background: #f7f7f7;
    border-radius: 15px;
    padding: 20px;
    margin: 30px;
    box-shadow: 2px 2px 5px #ccc;
  }

  #contact-us .btn-long {
    width: 80%;
    height: 60px;
    margin: 20px 10%;
    padding: auto;
  }
}

